import React from 'react';
import bcgSubpage from '../../../assets/bcg-white.jpg';
import bcgSubpageG from '../../../assets/bcg-green.jpg';
import { useTranslation } from 'react-i18next';
import './Privacy.scss';
import { SubpageHeader } from '../../components/SubpageHeader/SubpageHeader';

export const Privacy = ({ isNight }) => {
    const { t, i18n } = useTranslation('common');

    return (
        <div className={isNight ? 'products-container-n' : 'products-container'}>
            <SubpageHeader isNight={isNight} text='Polityka prywatnoci' />
            <div className={isNight ? 'privacy-content-n' : 'privacy-content'}>
                <p>Zgodnie art. 13 ust. 1 i 2 Rozporzdzenia Parlamentu Europejskiego i Rady (UE) 2016/679 z 27.04.2016 r. w sprawie ochrony osb fizycznych w zwizku z przetwarzaniem danych osobowych i w sprawie swobodnego przepywu takich danych oraz uchylenia dyrektywy 95/46/WE (oglne rozporzdzenie o ochronie danych) (Dz.Urz. UE L 119, s. 1), zwanym dalej: RODO, informuj Pani/Pana, e:</p>
                <p className='p-highlight'>Administratorem Pani/Pana danych osobowych jest:</p>
                <p><span>Printzilla Druk 3d Magorzata Trznadel</span>
                    W sprawie swoich danych osobowych moe si Pan/Pani skontaktowa si z Administratorem danych osobowych za pomoc:
                    poczty elektronicznej: info@nnvs.pl lub listu na adres siedziby: ul. Zauek 1, 66-615 agw</p>
                <p className='p-highlight'>Na podstawie art. 6 ust. 1 RODO Pani/Pana dane osobowe (imi, nazwisko, firma, nr PESEL, nr identyfikacji podatkowe, np. NIP lub REGON, adres, adres e-mail, numer telefonu, nr rachunku bankowego, adres siedziby) przetwarzamy w celu:</p>
                <ul>
                    <li>zawarcia umowy i wykonania jej (do dnia upywu terminu przedawnienia)</li>
                    <li>wypenienia obowizku prawnego cicego na administratorze, wynikajcego z przepisw podatkowych i rachunkowych (do dnia upywu terminu przedawnienia)</li>
                    <li>ustalenia, dochodzenia i egzekucji roszcze oraz obrony przed roszczeniami w postpowaniu przed sdami i innymi organami pastwowymi (do dnia upywu terminu przedawnienia)</li>
                    <li>analizy sprzeday i statystyki (do dnia, w ktrym upywa 5 lat od dnia wykonania umowy)</li>
                </ul>
                <p className='p-highlight'>Odbiorcami Pani/Pana danych osobowych mog by:</p>
                <ul>
                    <li>dostawcy systemw informatycznych,</li>
                    <li>dostawcy usug IT,</li>
                    <li>podmioty wiadczce na rzecz Spki usugi ksigowe, badania jakoci obsugi, dochodzenia nalenoci, usugi prawne, analityczne,</li>
                    <li>operatorzy pocztowi i kurierzy,</li>
                    <li>operatorzy systemw patnoci elektronicznych oraz banki w zakresie realizacji patnoci,</li>
                    <li>organy uprawnione do otrzymania Pani/Pana danych osobowych na podstawie przepisw prawa.</li>
                </ul>
                <p className='p-highlight'>Podanie przez Pana/Pani danych osobowych takich jak:</p>
                <p>imi, nazwisko, firma, nr PESEL, nr identyfikacji podatkowej (np. NIP lub REGON), adres, adres e-mail, numer telefonu, nr rachunku bankowego, adres siedziby jest dobrowolne, jednake niezbdne do zawarcia i wykonania umowy.</p>
                <p className='p-highlight'>Na podstawie RODO ma Pan/Pani prawo do:</p>
                <ul>
                    <li>dania dostpu do swoich danych osobowych (art. 15 RODO),</li>
                    <li>dania sprostowania swoich danych osobowych (art.16 RODO),</li>
                    <li>dania usunicia swoich danych osobowych, tzw. prawo do bycia zapomnianym (art. 17 RODO),</li>
                    <li>dania ograniczenia przetwarzania danych osobowych (art. 18 RODO),</li>
                    <li>wniesienia sprzeciwu wobec przetwarzania danych osobowych (art. 21 RODO),</li>
                    <li>dania przenoszenia danych osobowych (art. 20 RODO)</li>
                </ul>
                <p>Jeeli sdzi Pani/Pan, e przetwarzanie Pani/Pana danych osobowych narusza RODO ma Pani/Pan prawo zoenia skargi do organu nadzorczego, w szczeglnoci w pastwie czonkowskim swojego zwykego pobytu, swojego miejsca pracy lub miejsca popenienia domniemanego naruszenia. Obecnie organem nadzorczym jest GIODO, natomiast od dnia 25 maja 2018 r. penienie owej funkcji rozpocznie Prezes Urzdu Ochrony Danych Osobowych (PUODO).</p>
            </div>
        </div>
    );
};
