import React from 'react';
import soldiersWhite from '../../../assets/soldiers-white.jpg';
import soldiersBlack from '../../../assets/soldiers-black.jpg';

import './Home.scss'
import { CTA } from '../../components/CTA/CTA';
import { Quote } from '../../components/Quote/Quote';
import { Service } from '../../components/Service/Service';
import { AdvantagesWrapper } from '../../components/AdvantagesWrapper/AdvantagesWrapper';
import { Title } from '../../components/Title/Title';
import { useTranslation } from 'react-i18next';
import { SectionHome } from '../../components/SectionHome/SectionHome';

export const Home = ({ isNight }) => {
    const { t } = useTranslation('common');

    return (
        <>
            <div className='header-wrapper'>
                <h1 className='header-title'>{t('home.header')}
                    <span>{t('home.header-a')}</span></h1>
                <img src={!isNight ? soldiersWhite : soldiersBlack} className='header-bcg' alt='header night vision' />
            </div>
            <div className={isNight ? 'content-wrapper-n' : 'content-wrapper'}>
                <section>
                    <Title isNight={isNight} title='Poznaj nas' />
                    <div className='about-us'>
                        <span>{t('home.about')}</span>
                        <Quote isNight={isNight} />
                        <span>{t('home.about-1')}</span>
                    </div>
                </section>
                <CTA isNight={isNight} text={t('home.cta-1')} />
                <Service isNight={isNight} />
            </div>
            <SectionHome isNight={isNight} title={t("home.thermovision")} text1='Rozwj cyfrowych technik przetwarzania obrazu oraz metod detekcji ciepa, spowodowa skokow popraw urzdze termowizyjnych. Pocztkowo ograniczona przez konieczno stosowania skomplikowanych ukadw chodzenia i wydajnych ukadw zasilania w energi elektryczn, dzi  ju w  wersjach miniaturowych -znajduje zastosowania w wielu odlegych nieraz dziedzinach.' text2='Obecnie dostpne urzdzenia termowizyjne s niezastpione do szybkiej detekcji celu. Wraz z rozwojem technologii nastpuje ich miniaturyzacja przy zachowaniu odpowiedniej rozdzielczoci matrycy, jej czuoci i zapotrzebowaniu na prd. Zachcamy do zapoznania si w terenie z jakoci obrazu, wstpnie wybranego urzdzenia z naszego sklepu. Wierzymy, e obecnie prowadzone prace w kierunku poczenia z technik noktowizyjn i cyfrow pozwol zaoferowa w nieodlegej przyszoci jeszcze lepsze rozwizania.' textIMG='Zwykle do waciwego doboru urzdzenia nie wystarczy prosta ocena jego deklarowanych parametrw technicznych. Czsto urzdzenia zbudowane z tych samych matryc (przez rnych producentw) daj zupenie inne wraenia optyczne.  Jako uzyskiwanego obrazu wynika nie tylko z prostych parametrw i jakoci zastosowanych podzespow, ktre moemy sprawdzi w DTR (obiektyw, matryca, wywietlacz), ale te z czynnikw nieoczywistych jak np. zastosowanego oprogramowania.' reverse={false} />

            <AdvantagesWrapper isNight={isNight} />

            <SectionHome reverse={true} isNight={isNight} title={t("home.nightvision")} text1='Ch i potrzeba widzenia po zmroku od zawsze inspirowaa ludzi do poszukiwa nowych rozwiza. Pocztkowo stosowane silne rda wiata widzialnego okazay si niewystarczajce w zastosowaniach wymagajcych skrytego dziaania. W okresie II W, wprowadzono do uycia pierwsze urzdzenia umoliwiajce widzenie nocne po zastosowaniu silnych emiterw wiata niewidocznego dla ludzkiego oka.' textIMG='Pocztkowo due, niewygodne (np. plecakowe) i wymagajce duej iloci energii elektrycznej (silne dowietlacze arwkowe z filtrami IR) urzdzenia, ewoluoway by obecnie osign kompaktowe (350gram) rozmiary, z czasem pracy na jednej baterii ponad 40h. Postp techniczny w technice noktowizyjnej jest ogromny i dotyczy nie tylko samych wzmacniaczy wiata, ale rwnie elektroniki ich zasilajcej, dostpnej jasnej optyki, lekkich materiaw konstrukcyjnych a take ergonomii oraz precyzji wykonania. Zoono produktu powoduje, e wikszo osb ma due problemy z ocen parametrw sprztu i wyborem waciwego urzdzenia do wasnych zastosowa.' text2={'NextGen tworz ludzie z pasj, ktrzy chtnie zrozumiej Twoje potrzeby, przeprowadz Ci przez cay proces doboru urzdzenia i zaproponuj optymalne rozwizanie cenowe. Wyjanimy rnice w parametrach technicznych i uczciwie opowiemy o zaletach i ograniczeniach z nich wynikajcych. Nie ma rozwiza uniwersalnych, nadajcych si dla wszystkich, dlatego dbajc o wiadome decyzje naszych Klientw zachcamy do testw porwnawczych wybranych rozwiza. Rozumiemy Wasze pasje i potrzeby. Nasze urzdzenia s stosowane z powodzeniem przez myliwych, wojskowych, policjantw, pracownikw ochrony, strzelcw sportowych, astrofotografw, oraz entuzjastw ASG.'} />
        </>

    );
};
